	module.exports = {"data":{"markdownRemark":{"html":"<p>Whenever you create a new route or directive, you have to use <code>control+c</code> in your <em>grunt</em> terminal window to quit the grunt process and re-run <code>grunt serve</code> for your new route/directive to be included in your project’s <strong>index.html</strong>. Sometimes <em>grunt</em> can be a little finnicky and refuses to run if it thinks something is wrong with your project. Obviously you should try to fix the problem, but grunt’s errors aren’t very helpful so don’t worry too much—grunt usually will still run totally fine with the command <code>grunt serve --force</code>.</p>","fields":{"slug":"/miscellaneous/what-is-grunt/"},"frontmatter":{"title":"What Is Grunt"}}},"pathContext":{"slug":"/miscellaneous/what-is-grunt/"}}

